'use client'

import { useState, useRef } from 'react'
import { useSpeech } from './useSpeech'

interface ChatInputProps {
  onSendMessage: (message: string) => void
  disabled: boolean
}

export default function ChatInput({ onSendMessage, disabled }: ChatInputProps) {
  const [message, setMessage] = useState('')
  const textareaRef = useRef<HTMLTextAreaElement>(null)
  
  const {
    isRecording,
    isSupported,
    startRecording,
    stopRecording,
    transcript,
    speakText
  } = useSpeech()

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    if (message.trim() && !disabled) {
      onSendMessage(message.trim())
      setMessage('')
      if (textareaRef.current) {
        textareaRef.current.style.height = 'auto'
      }
    }
  }

  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault()
      handleSubmit(e)
    }
  }

  const handleTextareaChange = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
    setMessage(e.target.value)
    
    // Auto-resize textarea
    const textarea = e.target
    textarea.style.height = 'auto'
    textarea.style.height = Math.min(textarea.scrollHeight, 120) + 'px'
  }

  const toggleVoiceRecording = () => {
    if (isRecording) {
      stopRecording()
    } else {
      startRecording()
    }
  }

  // Update message when transcript changes
  useState(() => {
    if (transcript) {
      setMessage(transcript)
    }
  })

  return (
    <div className="border-t border-border bg-card p-4">
      <form onSubmit={handleSubmit} className="max-w-4xl mx-auto">
        <div className="flex items-end space-x-2">
          <div className="flex-1 relative">
            <textarea
              ref={textareaRef}
              value={message}
              onChange={handleTextareaChange}
              onKeyPress={handleKeyPress}
              placeholder="Type your message here... (Press Enter to send, Shift+Enter for new line)"
              className="w-full resize-none rounded-lg border border-input bg-background px-4 py-3 text-sm placeholder:text-muted-foreground focus:outline-none focus:ring-2 focus:ring-ring focus:border-transparent min-h-[50px] max-h-[120px]"
              disabled={disabled}
              rows={1}
            />
          </div>
          
          {/* Voice Button */}
          {isSupported && (
            <button
              type="button"
              onClick={toggleVoiceRecording}
              disabled={disabled}
              className={`px-4 py-3 rounded-lg font-medium transition-colors min-w-[100px] ${
                isRecording
                  ? 'bg-red-500 text-white hover:bg-red-600'
                  : 'bg-secondary text-secondary-foreground hover:bg-secondary/80'
              }`}
            >
              {isRecording ? 'Recording...' : 'Voice'}
            </button>
          )}
          
          {/* Send Button */}
          <button
            type="submit"
            disabled={disabled || !message.trim()}
            className="px-6 py-3 bg-primary text-primary-foreground rounded-lg font-medium hover:bg-primary/90 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
          >
            {disabled ? 'Sending...' : 'Send'}
          </button>
        </div>
        
        {/* Voice status indicator */}
        {isRecording && (
          <div className="mt-2 text-sm text-muted-foreground flex items-center">
            <div className="w-2 h-2 bg-red-500 rounded-full animate-pulse mr-2"></div>
            Listening... Speak now
          </div>
        )}
        
        {!isSupported && (
          <div className="mt-2 text-sm text-muted-foreground">
            Voice input is not supported in your browser
          </div>
        )}
      </form>
    </div>
  )
}
