'use client'

import { useState, useEffect } from 'react'
import ChatWindow from '@/components/ai/ChatWindow'
import ChatInput from '@/components/ai/ChatInput'
import ApiKeyModal from '@/components/ai/ApiKeyModal'

export interface ChatMessage {
  id: string
  sender: 'user' | 'ai'
  text: string
  timestamp: Date
}

export default function Home() {
  const [messages, setMessages] = useState<ChatMessage[]>([])
  const [isLoading, setIsLoading] = useState(false)
  const [apiKey, setApiKey] = useState<string>('')
  const [showApiKeyModal, setShowApiKeyModal] = useState(false)

  const handleSendMessage = async (message: string) => {
    if (!message.trim()) return

    // Check if API key is set
    if (!apiKey) {
      setShowApiKeyModal(true)
      return
    }

    const userMessage: ChatMessage = {
      id: Date.now().toString(),
      sender: 'user',
      text: message,
      timestamp: new Date()
    }

    setMessages(prev => [...prev, userMessage])
    setIsLoading(true)

    try {
      const { sendMessageToLLM } = await import('@/lib/ai')
      const response = await sendMessageToLLM([...messages, userMessage], apiKey)
      
      const aiMessage: ChatMessage = {
        id: (Date.now() + 1).toString(),
        sender: 'ai',
        text: response,
        timestamp: new Date()
      }

      setMessages(prev => [...prev, aiMessage])
    } catch (error) {
      const errorMessage: ChatMessage = {
        id: (Date.now() + 1).toString(),
        sender: 'ai',
        text: 'Sorry, I encountered an error. Please check your API key and try again.',
        timestamp: new Date()
      }
      setMessages(prev => [...prev, errorMessage])
    } finally {
      setIsLoading(false)
    }
  }

  const handleApiKeySubmit = (key: string) => {
    setApiKey(key)
    setShowApiKeyModal(false)
    // Store in localStorage for persistence
    localStorage.setItem('openrouter-api-key', key)
  }

  // Load API key from localStorage on component mount
  useEffect(() => {
    const savedKey = localStorage.getItem('openrouter-api-key')
    if (savedKey) {
      setApiKey(savedKey)
    }
  }, [])

  return (
    <div className="flex flex-col h-screen">
      {/* Header */}
      <header className="bg-card border-b border-border p-4">
        <div className="max-w-4xl mx-auto flex justify-between items-center">
          <h1 className="text-2xl font-bold">AI Assistant</h1>
          <button
            onClick={() => setShowApiKeyModal(true)}
            className="px-4 py-2 text-sm bg-secondary text-secondary-foreground rounded-lg hover:bg-secondary/80 transition-colors"
          >
            API Settings
          </button>
        </div>
      </header>

      {/* Main Chat Area */}
      <div className="flex-1 flex flex-col max-w-4xl mx-auto w-full">
        <ChatWindow messages={messages} isLoading={isLoading} />
        <ChatInput onSendMessage={handleSendMessage} disabled={isLoading} />
      </div>

      {/* API Key Modal */}
      {showApiKeyModal && (
        <ApiKeyModal
          onSubmit={handleApiKeySubmit}
          onClose={() => setShowApiKeyModal(false)}
          currentKey={apiKey}
        />
      )}
    </div>
  )
}
